body {
  min-height: 100%;
  padding: 0;
  margin: 0;
}

.container{
  text-align: center;
  position: absolute;
  height: 100%;
  width: 100%;
}

.topContainer{
  min-height: 100px;
}

.centerContainer{
  min-height: 200px;
  position: relative;
}

.btn{
  display: inline-block;
  background: #db1d29;
  color: #FFF;
  width: 200px;
  height: 200px;
  line-height: 120px;
  border-radius: 50%;
  border-color: transparent;
  text-align: center;
  font-weight: bold;
  font-size: 4em;
  vertical-align: middle;
  overflow: hidden;
  box-shadow: 10px 10px 10px rgba(0, 0, 0, 0.29);
  border-bottom: solid 2px #bd6565;
  transition: .01s;
  cursor: pointer;
}

.btn:active{
  -ms-transform: translateY(4px);
  -webkit-transform: translateY(4px);
  transform: translateY(4px);
  box-shadow: 0 0 1px rgba(0, 0, 0, 0.15);
  border-bottom: none;
}

.btn:focus{
  outline: none;
}

.restartbtn{
  display: inline-block;
  width: 280px;
  height: 180px;
  line-height: 120px;
  border-radius: 10%;
  border-color: transparent;
  text-align: center;
  font-weight: bold;
  font-size: 3em;
  vertical-align: middle;
  overflow: hidden;
  box-shadow: 10px 10px 10px rgba(0, 0, 0, 0.29);
  transition: .05s;
  cursor: pointer;
}

.restartbtn:active{
  -ms-transform: translateY(4px);
  -webkit-transform: translateY(4px);
  transform: translateY(4px);
  box-shadow: 0 0 1px rgba(0, 0, 0, 0.15);
  border-bottom: none;
}

.notification{
  color: #db1d29;
  font-size: 50px;
  font-Weight: bold;
  padding: 10;
  margin-Bottom: 10;
}

.restartMessage{
  color: black;
  font-size: 50px;
  font-Weight: bold;
  padding: 10;
  margin-Bottom: 10;
}

.themeButton{
  position: absolute;
  top:10px;
  right:10px;
  border: none;
  padding: 15px 32px;
  outline: none;
  font-size: 16px;
  margin: 4px 2px;
  cursor: pointer;
}



